import React, { useState, useEffect } from "react";
import { Card, Form } from "react-bootstrap";
import ControlsMap from "../../../Properties/PropertiesConfiguration";
import RichtextEditorPropertiesJson from "../../../Properties/RichtextEditorPropertiesJson";
import CollapsibleComponent from "../../../../ReusableComponents/CollapseCard/CollapseCard";
import styles from "../../../LeftNavigation/DesignerControls/BasicControls.module.css";
import ColorPicker from "../../../../ReusableComponents/ColorPicker/ColorPicker";
import ToggleBtn from "../../../../ReusableComponents/ToggleBtn/ToggleBtn";
import ToggleState1 from "../../../../Icons/ToggleState1";
import ToggleState2 from "../../../../Icons/ToggleState2";
import RedCross from "../../../../Icons/RedCross";
import ControlLabel from "../../../../ReusableComponents/ControlLabel/ControlLabel";
import { regexValidator } from "../../../../Resources/globalFunctions";
import CustomizedAlert from "../../../../ReusableComponents/Alert/CustomizedAlert";
import { useTranslation } from "react-i18next";
import ErrorMessageComponent from "../../../../ReusableComponents/ErrorMessageComponent/ErrorMessageComponent";
import { EventListjson } from "../../../../ReusableComponents/Events/EventList";
import Events from "../../../../ReusableComponents/Events/Events";
import { validateCustomControlId } from "../../RequestLogic";
import { updateUserId } from "../../Logic/UserIdLogic";
import { FontWeightListJson } from "../../../Properties/FontWeightList";

import Dots from '../../../../Icons/ThreeDots.jsx';
import MappingFile from '../../../../ReusableComponents/Mapping/MappingFile';
import CancelOutlinedIcon from '@material-ui/icons/CancelOutlined';
import {removeMappingFromControl} from "../../IFormDesignerLogic";
import { GetStateFromData } from "../../../CustomHooks/CustomHooks.js";

const RichtextEditorProperties = (props) => {
  const contexts = GetStateFromData(props.containerId);
	  const {updateJson, deleteControl, showAlertConfirmNotifications,fromQuickProps,filteredDataObjectCopy,setCurrentMappedDO }=contexts.contexts;
	  const { updatedComponentInFormState } =updateJson;
    const translationStyles1=window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"; 
  
  const { t } = useTranslation(['styles', 'translation']);
  const [customState, setCustomState] = useState();
  const [errorMessageId, setErrorMessageId] = useState(false);
  const [alertData, setAlertData] = useState({
    open: false,
    showConfirm: "showNotification"
});

  const [mappingField,setMappingField] = useState(false);

  useEffect(()=>{
    if(fromQuickProps){
        setMappingField(true)
    }
},[fromQuickProps])

  function mappingOpen(){
		setMappingField(true);
	}

  function handleCombo(comboId) {
    var select = document.getElementById(comboId);
    var propertyName = select.getAttribute("name");
    var optionTag = select.options[select.selectedIndex];

    handleProperty(propertyName, optionTag.value);
  }

  function handleProperty(propertyName, propertyValue, event, inputBoxId) {
    
    try {
      if(propertyName === "CustomId")
      {
        if(propertyValue !== props.selectedControl.style.CustomId ){
          var element=event.target;
          var jsonToSend={
            "customControlId":propertyValue,
            "controlid":props.selectedControl.id
          }
          validateCustomControlId(jsonToSend).then((response)=>{
            if(response && response.data && response.data.status === "0"){
              let updatedJson = updateUserId(
                props.selectedControl, // whole control Json
                propertyValue, //newly defined id
              );
              if(updatedJson){             
                updatedComponentInFormState(props.selectedControl.id , props.selectedControl);
              }
            }
            else if(response && response.data && response.data.status === "-1")
            {
              showAlertConfirmNotifications({
                'open':true,
                'showConfirm':'showNotification',
                'alertType':'error',
                'alertContentKey':"CustomControl Id already Exist"
              });
              element.value=props.selectedControl.style.CustomId || '';
            }
          }).catch(function (error) {
            element.value=props.selectedControl.style.CustomId || '';           
            console.error('Server side error '+error);
        });
        }
      }
      
      else {
        props.selectedControl.style[propertyName] = propertyValue;

         if(propertyValue==="Regular Italic"){
          props.selectedControl.style[propertyName] = 300;
          props.selectedControl.style["FontStyle"] = "italic";
          props.selectedControl.style["LabelWeightStyle"] = propertyValue;
         }
        else if(propertyValue==="Semibold"){
            props.selectedControl.style[propertyName] = 600;
            props.selectedControl.style["FontStyle"] = "normal";
            props.selectedControl.style["LabelWeightStyle"] = propertyValue;
        }
        else if(propertyValue==="Bold"){
            props.selectedControl.style[propertyName] = "bold";
            props.selectedControl.style["FontStyle"] = "normal";
            props.selectedControl.style["LabelWeightStyle"] = propertyValue;
        }
        else if(propertyValue==="Regular"){
            props.selectedControl.style[propertyName] = 300;
            props.selectedControl.style["FontStyle"] = "normal";
            props.selectedControl.style["LabelWeightStyle"] = propertyValue;
        }
        else if(propertyValue==="Bold Italic"){
            props.selectedControl.style[propertyName] = "bold";
            props.selectedControl.style["FontStyle"] = "italic";
            props.selectedControl.style["LabelWeightStyle"] = propertyValue;
        }
        else if(propertyValue==="Light Italic"){
            props.selectedControl.style[propertyName] = 100;
            props.selectedControl.style["FontStyle"] = "italic";
            props.selectedControl.style["LabelWeightStyle"] = propertyValue;
        }
        else if(propertyValue==="Semibold Italic"){
            props.selectedControl.style[propertyName] = 600;
            props.selectedControl.style["FontStyle"] = "italic";
            props.selectedControl.style["LabelWeightStyle"] = propertyValue;
        }
        else if(propertyValue==="Light"){
            props.selectedControl.style[propertyName] = 100;
            props.selectedControl.style["FontStyle"] = "normal";
            props.selectedControl.style["LabelWeightStyle"] = propertyValue;
        }
        if(propertyValue==="" && propertyName==="FontWeight"){
          props.selectedControl.style["FontStyle"] = "normal";
          props.selectedControl.style["LabelWeightStyle"] = propertyValue;
        }
        
        updatedComponentInFormState(
          props.selectedControl.id,
          props.selectedControl
        );
        props.applyChanges(props.formData);
        setErrorMessageId(false);
      }
    } catch (e) {
      console.log(e);
    }
  }

  function validateRegex(propertyName, propertyValue, event, inputBoxId) {
    try {
      let passValue=event?.key;
      if(propertyName==="CustomId"){
        if(event.target.selectionEnd === event.target.selectionStart){
          passValue=[propertyValue.slice(0,event.target.selectionEnd)+event.key + propertyValue.slice(event.target.selectionEnd) ].join("");
        }
        else {
          let stringArray = propertyValue.split("");
          stringArray.splice(event.target.selectionStart, event.target.selectionEnd - event.target.selectionStart);
          stringArray.splice(event.target.selectionStart, 0, ...event?.key.split(""));
          passValue = stringArray.join("");
        }
      }
      if (
        event &&
        event.which !== 8 &&
        event.which !== 46 &&
        event.code !== "Tab" &&
        !regexValidator(
          passValue,
          ControlsMap[propertyName]["regexType"]
        )
      ) {
        event.preventDefault();
        setAlertData({
          ...ControlsMap["customizedAlertNotificationData"],
          open: true,
          alertContentKey: "RegexErrorMessage",
        });
      } else {
        setAlertData({
          showConfirm: "showNotification",
          open: false,          
        });
        setErrorMessageId(false);
      }
    } catch (e) {
      console.log("Error while validating regex");
    }
  }

  const updateEventData = (eventJson) => {
    var count = Object.keys(eventJson).length;
    if (count > 0) {
      var Events = {};
      Events["Events"] = eventJson;
      props.selectedControl["event"] = Events;
    }
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
  };

  function handlePropertyText(propertyName, propertyValue) {
   
    props.selectedControl.style[propertyName] = propertyValue;
    updatedComponentInFormState(
        props.selectedControl.id,
        props.selectedControl
      );
    props.applyChanges(props.formData);
  }

  function deleteProperty(propertyName) {
    delete props.selectedControl.style[propertyName];
    updatedComponentInFormState(
      props.selectedControl.id,
      props.selectedControl
    );
    props.applyChanges(props.formData);
  }

  function handleToggle(e, field) {
    const value = e.target.value;
    if(field === "Enable") {
      props.selectedControl.style["previousEnable"] = value;
    }
    if(field === "ReadOnlyStyle") {
      props.selectedControl.style["previousReadOnly"] = value;
    }
    if(field === "ReadOnly") {
      props.selectedControl.style["previousReadOnlyValue"] = value;
    }
    handleProperty(field, value);
  }

  if (RichtextEditorPropertiesJson && ControlsMap) {
    var CollapsibleItems = [];
    Object.keys(RichtextEditorPropertiesJson).forEach((key, index) => {
      var collapseComponentBody = [];
      Object.keys(RichtextEditorPropertiesJson[key]).forEach((property) => {
        if (ControlsMap[property] && ControlsMap[property].type) {
          if (property === "Column1") {
            props.selectedControl.children.map((col, index) => {
              collapseComponentBody.push(
                prepareHtmlTag(
                  col.style.ColumnName,
                  ControlsMap[property].type,
                  col.id
                )
              );
            });
          } else {
            collapseComponentBody.push(
              prepareHtmlTag(property, ControlsMap[property].type)
            );
          }
        }
      });
      if (CollapsibleItems === []) {
        CollapsibleItems.push(
          <CollapsibleComponent
            id={index + ""}
            heading={key}
            body={collapseComponentBody}
          />
        );
      }

      CollapsibleItems.push(
        <CollapsibleComponent
          id={index + ""}
          heading={key}
          body={collapseComponentBody}
        />
      );
    });
  }

  function prepareHtmlTag(key, type, colId) {
    switch (type.toLowerCase()) {
      case "combo":
        return (
          <>
            <div className={styles.parentDiv} style={translationStyles1?{display: "flex","justify-content": "space-between"}:null}>
              <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
              <div className={styles.RightLabelSelectDiv1}>
           
               {key!== "FontWeight" ?
                <>
                <select
                  className={styles.RightLabelSelect1}
                  name={key}
                  key={props.selectedControl.id}
                  id={props.selectedControl.id + key}
                  onChange={() => handleCombo(props.selectedControl.id + key)}
                >
                  {ControlsMap[key].children.map(
                    (optionTagData, optionCount) => {
                      if (ControlsMap[key].children[optionCount].Title) {
                        if (
                          props &&
                          props.selectedControl &&
                          props.selectedControl.style &&
                          props.selectedControl.style[key] ===
                            ControlsMap[key].children[optionCount].Value
                        ) {
                          return (
                            <option
                            style={ControlsMap[key].children[optionCount]?.style}
                              value={
                                ControlsMap[key].children[optionCount].Value
                              }
                              selected
                            >                              
                              {t(`translation:${ControlsMap[key].children[optionCount].Title}`)}
                            </option>
                          );
                        } else {
                          return (
                            <option
                            style={ControlsMap[key].children[optionCount]?.style}
                              value={
                                ControlsMap[key].children[optionCount].Value
                              }
                            >                              
                              {t(`translation:${ControlsMap[key].children[optionCount].Title}`)}
                            </option>
                          );
                        }
                      }
                    }
                  )}
                </select>
                 </>
          :
          <>
          <select id={props.selectedControl.id + key} className={styles.RightLabelSelect1}
          name={key}
          key={props.selectedControl.id}
          onChange={() => handleCombo(props.selectedControl.id + key)}
          >
          {props.selectedControl.style && props.selectedControl.style["FontFamily"]
          ?
          <>
          {FontWeightListJson[props.selectedControl.style["FontFamily"]].children.map(
                        (optionTagData, optionCount) => {
                          if (FontWeightListJson[props.selectedControl.style["FontFamily"]].children[optionCount].Title) {
                            return (
                              <>
                      {FontWeightListJson[props.selectedControl.style["FontFamily"]].children[optionCount].Value ===
                  props.selectedControl.style["LabelWeightStyle"]
                       ? (
                        <option
                          value={
                            FontWeightListJson[props.selectedControl.style["FontFamily"]].children[optionCount].Value
                          }
                          selected
                        >
                          {t(`translation:${FontWeightListJson[props.selectedControl.style["FontFamily"]].children[optionCount].Title}`)}
                          
                        </option>
                      ) : (
                        <option
                          value={
                            FontWeightListJson[props.selectedControl.style["FontFamily"]].children[optionCount].Value
                          }
                        >
                          {t(`translation:${FontWeightListJson[props.selectedControl.style["FontFamily"]].children[optionCount].Title}`)}                          
                          
                        </option>
                      )}
                    </>
                  );
                }
              }
              )}
              </>
              :

        <>
        {FontWeightListJson["Open Sans"].children.map(
        (optionTagData, optionCount) => {
        if (FontWeightListJson["Open Sans"].children[optionCount].Title) {
          return (
            <>
              {FontWeightListJson["Open Sans"].children[optionCount].Value ===
             props.selectedControl.style["LabelWeightStyle"] 
              ? (
                <option
                  value={
                    FontWeightListJson["Open Sans"].children[optionCount].Value
                    
                  }
                  selected
                >
                  {t(`translation:${FontWeightListJson["Open Sans"].children[optionCount].Title}`)}                          
                          
                  
                </option>
              ) : (
                <option
                  value={
                    FontWeightListJson["Open Sans"].children[optionCount].Value
                  }
                >
                  {t(`translation:${FontWeightListJson["Open Sans"].children[optionCount].Title}`)}                   
                </option>
              )}
            </>
          );
        }
        }
        )}
        </>


        }
        </select>
        </>
        }  


              </div>
            </div>
            {customState === "CustomState" && key === "ToggleStates" ? (
              <>
                <div className={styles.parentDiv2} style={translationStyles1?{display: "flex","justify-content": "space-between"}:null}>
                  <span className={styles.leftLabel}>State1</span>{" "}
                  <ToggleState1 />
                  <input
                    aria-label={"Control Value1"}
                    type="text"
                    defaultValue={props.selectedControl.style["ToggleState1"]}
                    className={styles.RightLabelSelect1}
                    onChange={(event) =>
                      handleProperty("ToggleState1", event.target.value)
                    }
                  />
                </div>
                <div className={styles.parentDiv2} style={translationStyles1?{display: "flex","justify-content": "space-between"}:null}>
                  <span className={styles.leftLabel}>State2</span>
                  <ToggleState2 />
                  <input
                    aria-label={"Control Value2"}
                    type="text"
                    defaultValue={props.selectedControl.style["ToggleState2"]}
                    className={styles.RightLabelSelect1}
                    onChange={(event) =>
                      handleProperty("ToggleState2", event.target.value)
                    }
                  />
                </div>
              </>
            ) : null}
          </>
        );

      case "checkbox":
        return props.selectedControl && props.selectedControl.style ? (
          <div
            className={[
              styles.textAreaParentDiv,
              styles.textAreaParentDiv1,
            ].join(" ")}
          >
            <input
              aria-label={"Control Value3"}
              id={ControlsMap[key].label}
              key={props.selectedControl.id}
              type="checkbox"
              defaultChecked={
                props.selectedControl.style[key] === "true" ? true : false
              }
              onClick={() =>
                handleProperty(
                  key,
                  props.selectedControl.style[key] === "true" ? "false" : "true"
                )
              }
            />
            <label
              aria-label={ControlsMap[key].label}
              for={ControlsMap[key].label}
              className={styles.RichtextEditorPropertiesCheckboxLable}
            >
              {t(`translation:${ControlsMap[key].label}`)}
            </label>
          </div>
        ) : null;

        case "text" : return(
					<div className={styles.parentDiv}>
					<span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
					<div className={styles.RightTextBox}>
						{
							<input aria-label={"Control Value5"} type="text"  defaultValue={props.selectedControl.style[key]} 
							key={props.selectedControl.id}
							id={props.selectedControl.id+key} 
							className={[styles.RightLabelTextBox, errorMessageId === (props.selectedControl.id+key) ? styles.inputBoxErrorStyle : null].join(" ")}
							onKeyPress={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
				     		onKeyUp={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
					    	onBlur={(event)=>handleProperty(key, event.target.value,event)}
                
							/>  
						}
						
					</div>
					{errorMessageId === (props.selectedControl.id+key) ? <ErrorMessageComponent errorMessage="non-permissible value"/> : null}
					</div>
				)

      case "maxHeightText":
        return (
          <div className={styles.parentDiv2} style={{ marginLeft:'8px',marginRight:'8px'}}>
            <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
            <div className={styles.RightLabelSelectDiv1}>
              <div className={styles.TableHeightinput}>
                <span
                  className={[
                    styles.RightLabelTextInputSpan,
                    styles.RightLabelTextInputSpan2,
                  ].join(" ")}
                >
                  px
                </span>
                <input
                  aria-label={"Control Value6"}
                  type="text"
                  key={props.selectedControl.id}
                  defaultValue={
                    props.selectedControl && props.selectedControl.style
                      ? props.selectedControl.style["MaxHeight"]
                      : null
                  }
                  className={[
                    styles.RightLabelTextInputTable,
                    errorMessageId === props.selectedControl.id + key
                      ? styles.inputBoxErrorStyle
                      : null,
                  ].join(" ")}
                 
                  onKeyPress={(event) =>
                    validateRegex(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  onKeyUp={(event) =>
                    validateRegex(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  onBlur={(event) =>
                    handlePropertyText(key, event.target.value)
                  }
                />
              </div>
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "colorpicker":
        return (
          <div className={["colorpicker", styles.parentDiv2].join(" ")}>
            {t(`translation:${ControlsMap[key].label}`)}
            <ColorPicker
              key={props.selectedControl.id}
              color={props.selectedControl.style[key]}
              handleProperty={handleProperty}
              propertyName={key}
            />
          </div>
        );

      case "toggle":
        return (
          <div className={styles.parentDiv2} style={translationStyles1?{display: "flex","justify-content": "space-between"}:null}>
            <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
            <div className={translationStyles1 ? styles.toggleCheckboxArabic : styles.toggleCheckbox}>
              <ToggleBtn
                key={props.selectedControl.id}
                toggleState={props.selectedControl.style[key]}
                handleToggle={handleToggle}
                field={key}
                id={props?.selectedControl?.style?.CustomId ? props?.selectedControl?.style?.CustomId : props?.selectedControl?.id}
              />
            </div>
          </div>
        );

        case "text_top_bottom":
          return (
            <Form.Group className={styles.parentDivTopBottom}>
              <div>
                <span className={styles.mappingLabel}>
                  {t(`translation:${ControlsMap[key].label}`)}
                </span>
              </div>
  
              {key==="Mapping" ?
            <>            
            <div tabIndex="0" style={{display:"flex"}} title={props.selectedControl.style[key]}>
						<input type="text" aria-label={"Control Value12"}defaultValue={props.selectedControl.style[key]}
            style={{width:"100%"}} 
						key={props.selectedControl.id + props.selectedControl.style[key]}
            disabled={true}
						id={props.selectedControl.id+key} 
						className={[styles.overflowEll, styles.fullWidthTextBox, errorMessageId === (props.selectedControl.id+key) ? styles.inputBoxErrorStyle : null].join(" ")}
						onKeyPress={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
						onKeyUp={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
						onBlur={(event)=>handleProperty(key, event.target.value,event)}
						/>
            {props?.selectedControl?.dataclass ? <span title={t("translation: Remove Mapping")} onClick={()=>{removeMappingFromControl(props.selectedControl,updatedComponentInFormState,filteredDataObjectCopy,setCurrentMappedDO)}} style={!translationStyles1?{position:"absolute",right:"15%",paddingTop:"7px", paddingRight: "5px"}:{position:"absolute",left:"15%",paddingTop:"7px", paddingLeft: "5px"}}>
                    <CancelOutlinedIcon/>
                  </span> : null}
						<span title={t("translation: View Data Objects")} className={styles.mappingDotsIcon} style={translationStyles1?{borderLeft:"1px solid #C4C4C4",height:"unset"}:{height:"unset"}} onClick={()=>mappingOpen()} ><Dots/></span>
					</div> 
            </>
            :
            <>
              <div>
                <input
                  aria-label={"Control Value8"}
                  type="text"
                  id={props.selectedControl.id + key}
                  defaultValue={props.selectedControl.style[key]}
                  key={props.selectedControl.id}
                  className={styles.fullWidthTextBox}
                  onKeyPress={(event) =>
                    validateRegex(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  onKeyUp={(event) =>
                    validateRegex(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  onBlur={(event) => handleProperty(key, event.target.value, event)}
                />
              </div>
              </>}
              {errorMessageId === props.selectedControl.id + key ? (
                <ErrorMessageComponent errorMessage="non-permissible value" />
              ) : null}
            </Form.Group>
          );    

          case "textbox":
            return (
              <div>
                <div className={styles.leftLabelForTextBox}>
                  <div className={styles.labelForLabel} >{t(`translation:${ControlsMap[key].label}`)} </div>
                  <input 
                    aria-label={"Control Value10"}
                    type="text"  
                    defaultValue={props.selectedControl.style[key]} 
                    key={
                      props.selectedControl.id +
                      props.selectedControl.style[key]
                    }
                    maxLength={window?.depsatkur_configs?.label_max_length}
                    id={props.selectedControl.id+key}
                    className={[styles.textBoxLayoutForLable, errorMessageId === (props.selectedControl.id+key) ? styles.inputBoxErrorStyle : null].join(" ")}
                    onKeyPress={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
                    onKeyUp={(event)=>validateRegex(key, event.target.value,event,props.selectedControl.id+key)}
                    onBlur={(event)=>handleProperty(key, event.target.value, event)}
                  />
                </div>
                {errorMessageId === props.selectedControl.id + key ? (
                  <ErrorMessageComponent errorMessage="non-permissible value" />
                ) : null}
              </div>
            );


      case "textarea":
        return (
          <div className={styles.textAreaParentDiv}>
            <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
            <div>
              <textarea
                aria-label={props.selectedControl.style[key]}
                defaultValue={
                  props && props.selectedControl && props.selectedControl.style
                    ? props.selectedControl.style[key]
                    : ""
                }
                key={props.selectedControl.id}
                className={[
                  styles.textAreaDivRichText,
                  errorMessageId === props.selectedControl.id + key
                    ? styles.inputBoxErrorStyle
                    : null,
                ].join(" ")}
                
                onKeyPress={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onKeyUp={(event) =>
                  validateRegex(
                    key,
                    event.target.value,
                    event,
                    props.selectedControl.id + key
                  )
                }
                onBlur={(event) => handlePropertyText(key, event.target.value)}  
              />
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "fullwidthtextinput":
        return (
          <div className={styles.textAreaParentDiv}>
            <span className={styles.leftLabel}>{t(`translation:${ControlsMap[key].label}`)}</span>
            <div className={[styles.parentDiv].join(" ")}>
              {key === "ToggleLabel" ? (
                <input
                aria-label={"Control Value11"}
                  type="text"
                  defaultValue={
                    props &&
                    props.selectedControl &&
                    props.selectedControl.style &&
                    props.selectedControl.style[ControlsMap[key].label]
                      ? props.selectedControl.style[ControlsMap[key].label]
                      : props.selectedControl.label
                  }
                  key={props.selectedControl.id}
                  className={[
                    styles.fullWidthInputText,
                    errorMessageId === props.selectedControl.id + key
                      ? styles.inputBoxErrorStyle
                      : null,
                  ].join(" ")}
                  
                  onKeyPress={(event) =>
                    validateRegex(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  onKeyUp={(event) =>
                    validateRegex(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  onBlur={(event) => handlePropertyText(key, event.target.value)} 
                  
                />
              ) : (
                <input
                  aria-label={"Control Value12"}
                  type="text"
                  key={props.selectedControl.id}
                  defaultValue={
                    props &&
                    props.selectedControl &&
                    props.selectedControl.style
                      ? props.selectedControl.style[ControlsMap[key].label]
                      : ""
                  }
                  className={[
                    styles.fullWidthInputText,
                    errorMessageId === props.selectedControl.id + key
                      ? styles.inputBoxErrorStyle
                      : null,
                  ].join(" ")}
                  onKeyPress={(event) =>
                    handleProperty(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                  onKeyUp={(event) =>
                    handleProperty(
                      key,
                      event.target.value,
                      event,
                      props.selectedControl.id + key
                    )
                  }
                />
              )}
              {ControlsMap[key].label === "Mapping" ? (
                <span
                  className={styles.redCrossSpan}
                  onClick={() => deleteProperty(ControlsMap[key].label)}
                >
                  {props.selectedControl &&
                  props.selectedControl.style &&
                  props.selectedControl.style[ControlsMap[key].label] ? (
                    <RedCross />
                  ) : null}
                </span>
              ) : null}
            </div>
            {errorMessageId === props.selectedControl.id + key ? (
              <ErrorMessageComponent errorMessage="non-permissible value" />
            ) : null}
          </div>
        );

      case "header":
        return (
          <p className={styles.propertiesHeader} style={window?.depsatkur_configs?.selectedLanguage==="ar" || window?.depsatkur_configs?.selectedLanguage==="ar-SA" ?{textAlign:"right"}:null}>{t(`translation:${ControlsMap[key].label}`)}</p>
        );
      case "hr":
        return <>
        <div className={styles.hrTagTable}>
                     <hr/>
                      </div>
        </>
        ;

      case "header":
        return (
          <p className={styles.propertiesHeader} style={window?.depsatkur_configs?.selectedLanguage==="ar" || window?.depsatkur_configs?.selectedLanguage==="ar-SA" ?{textAlign:"right"}:null}>{t(`translation:${ControlsMap[key].label}`)}</p>
        );

      case "textareamandatorymessage":
        return (
          <>
            {props.selectedControl.style["Mandatory"] !== undefined &&
            props.selectedControl.style["Mandatory"] !== null &&
            props.selectedControl.style["Mandatory"] === "true" ? (
              <div className={styles.textAreaParentDiv}>
                <div className={styles.errorMandatoryMessage}>
                  {t(`translation:${"Error message if not filled"}`)} 
                </div>
                <div className={styles.leftLabelForTextBox}>
                  <p>{t(`translation:${ControlsMap[key].label}`)} </p>
                  <textarea
                    aria-label={props.selectedControl.style[key]}
                    className={[
                      styles.textBoxLayoutStyletextAreaSpecialCharacter,
                      errorMessageId === props.selectedControl.id + key
                        ? styles.inputBoxErrorStyle
                        : null,
                    ].join(" ")}
                    defaultValue={props.selectedControl.style[key]}
                    key={props.selectedControl.id}
                    id={props.selectedControl.id + key}
                    placeholder={"Enter Error Message"}
                    
                    onKeyPress={(event) =>
                      validateRegex(
                        key,
                        event.target.value,
                        event,
                        props.selectedControl.id + key
                      )
                    }
                    onKeyUp={(event) =>
                      validateRegex(
                        key,
                        event.target.value,
                        event,
                        props.selectedControl.id + key
                      )
                    }
                    onBlur={(event) => handlePropertyText(key, event.target.value)} 
                  ></textarea>
                </div>
                {errorMessageId === props.selectedControl.id + key ? (
                  <ErrorMessageComponent errorMessage="non-permissible value" />
                ) : null}
              </div>
            ) : null}
          </>
        );
      case "eventbutton":
        return (
          <Events
          key={props.selectedControl.id + "_eventsProperty"}
          json={EventListjson}
          value={props.selectedControl.event}
          elemtype={props.selectedControl.elem_type}
          updateEventData={(eventJson) => updateEventData(eventJson)}
          ></Events>
        );
      default: return null;
    }
  }

  return (
    <>
      
      <ControlLabel
        label="Richtext Editor"
        handleDelete={deleteControl}
        id={props.selectedControl.id}
        customId = {props?.selectedControl?.style?.CustomId}
      />
      <div className="propertiesBody">
      <Card.Body className={styles.cardBodyButtonProperties}>
        {CollapsibleItems.map((item, index) => {
          return item;
        })}
      </Card.Body>
      </div>
      {mappingField &&   <MappingFile stateOpen={true}
        setMappingField={setMappingField}
          controlData={props.selectedControl}
        elementType={props.selectedControl.elem_type}
      /> }
      {
              alertData.open === true &&
            <CustomizedAlert alertData={alertData} />
            }
    </>
  );
};

export default RichtextEditorProperties;
